import React, { useState } from "react";
import PackDataService from "../services/pack.service";

const PackAdd = () => {
  const initialPackState = {
    id: null,
    name: "",
    gender: "",
    birthday: "",
  };
  const [pack, setPack] = useState(initialPackState);
  const [submitted, setSubmitted] = useState(false);
  const [errors, setErrors] = useState({
    name: " ",
    gender: " ",
    birthday: " ",
  });
  const [hasError, setHasError] = useState(true);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setPack({ ...pack, [name]: value });
    checkError(name, value);
  };

  const checkError = (name, value) => {
    let newErrors = { ...errors };
    switch (name) {
      case "name":
        newErrors.name = value.length < 1 ? "No name given!!" : "";
        break;
      case "gender":
        newErrors.gender =
          value === "female" || value === "male"
            ? ""
            : "Gender not male or female";
        break;
      case "birthday":
        newErrors.birthday = Date.parse(value) ? "" : "Invalid date given!";
        break;
      default:
        break;
    }
    setErrors(newErrors);
    setHasError(showErrors(newErrors).length > 0);
  };

  const showErrors = (errors) => {
    return Object.values(errors).filter((val) => val.length > 0);
  };

  const savePack = () => {
    var data = {
      name: pack.name,
      gender: pack.gender,
      birthday: pack.birthday,
    };
    if (hasError) {
      console.error("Invalid Form");
    } else {
      PackDataService.create(data)
        .then((response) => {
          setPack({
            id: response.data.id,
            name: response.data.name,
            gender: response.data.gender,
            birthday: response.data.birthday,
          });
          setSubmitted(true);
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    }
  };

  const newPack = () => {
    setPack(initialPackState);
    setSubmitted(false);
  };

  return (
    <div className="submit-form">
      {submitted ? (
        <div>
          <h4>Your pack has been submitted successfully</h4>
          <button className="btn btn-success" onClick={newPack}>
            Add another one
          </button>
        </div>
      ) : (
        <div className="form">
          <div className="form-group">
            <label htmlFor="name" className="col-form-label">
              Name
            </label>
            <input
              className="form-control"
              id="name"
              required
              value={pack.name}
              onChange={handleInputChange}
              name="name"
            />
          </div>

          {showErrors(errors) &&
            showErrors(errors).map((error) =>
              error.trim().length > 0 ? (
                <div className="alert alert-danger" role="alert">
                  {error}
                </div>
              ) : (
                ""
              )
            )}

          <button
            onClick={savePack}
            disabled={hasError}
            className="btn btn-success"
          >
            Submit
          </button>
        </div>
      )}
    </div>
  );
};

export default PackAdd;
